@media only screen and (max-width: 768px){

    .wm-container{
        font-family: 'Gowun Batang', serif;
        width: 100vw;
    }
    
    .wm-main-container{
        margin-top: 6rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .a-header{
        font-size: 1.5rem;
        font-weight: bold;
    
    }
    .image-area{
        width: 100%;
        height: 300px;
        object-fit: contain;
    }
    
    .image-area img{
        width: 90%;
    }
    
    .tagline{
        margin-top: 2vh;
        width: 80%;
        font-size: 1.2rem;
    }
    
    .product-area{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 4vh;
        margin-top: 4vh;
        padding: 10px;
    }
    .a-div1{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        
    }
    .product{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .product-img{
        width: 70%;
        object-fit:contain;
    }
    
    .product-title{
        color: black;
        width: 100%;
    
    }
    
    .prices{
        font-size: 1rem;
    }
    
  
    
    }